<template>
  <div>
    <div @click="toFork">to FORK</div>
    <div @click="toFeng">to toFeng</div>
    <div @click="ToLiuZong">to LiuZong</div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Fork’s indexVue '
    }
  },
  methods: {
    toFork () {
      this.$router.push({
        name: 'Fork'
      })
    }
    ,
    toFeng () {
      this.$router.push({
        name: 'Feng'
      })
    }
    ,
    ToLiuZong () {
      this.$router.push({
        name: 'LiuLin'
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



